'use client';

import { useState, useEffect } from 'react';
import axios from '@/lib/axios';
import Image from 'next/image';
import Link from 'next/link';
import { useParams, useRouter } from 'next/navigation';
import { toast } from 'sonner';
import { 
    Star, Minus, Plus, ShoppingCart, ArrowLeft, CheckCircle2, ArrowRight
} from 'lucide-react';

import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { ProdukResto } from '@/types';

export default function ProductDetailPage() {
    const params = useParams();
    const router = useRouter();
    const id = params.id;

    const [product, setProduct] = useState<ProdukResto | null>(null);
    const [relatedProducts, setRelatedProducts] = useState<ProdukResto[]>([]);
    const [loading, setLoading] = useState(true);
    
    // State Transaksi
    const [quantity, setQuantity] = useState(1);

    // Fetch Data
    useEffect(() => {
        const fetchData = async () => {
            if (!id) return;
            try {
                setLoading(true);
                const resDetail = await axios.get(`/api/produkRestos/${id}`);
                setProduct(resDetail.data);

                const resAll = await axios.get('/api/produkRestos');
                const others = resAll.data
                    .filter((p: ProdukResto) => p.id !== Number(id))
                    .slice(0, 4); // Ambil 4 saja
                setRelatedProducts(others);

            } catch (error) {
                console.error(error);
                toast.error("Produk tidak ditemukan.");
                router.push('/pelanggan/produk');
            } finally {
                setLoading(false);
            }
        };
        fetchData();
    }, [id, router]);

    const handleIncrement = () => setQuantity(prev => prev + 1);
    const handleDecrement = () => setQuantity(prev => (prev > 1 ? prev - 1 : 1));

    const formatRupiah = (num: number) => 
        new Intl.NumberFormat('id-ID', { style: 'currency', currency: 'IDR', minimumFractionDigits: 0 }).format(num);

    // Fungsi Order (Sementara hanya notifikasi, karena belum ada fitur Cart di DB pelanggan)
    const handleOrder = () => {
        toast.success(`Berhasil menambahkan ${quantity} ${product?.nama_produk} ke pesanan!`, {
            description: "Silakan hubungi kasir atau lanjutkan belanja.",
            icon: <CheckCircle2 className="text-green-600" />
        });
    };

    if (loading) return <div className="min-h-screen bg-white flex items-center justify-center">Memuat...</div>;
    if (!product) return null;

    return (
        <div className="min-h-screen bg-white font-sans text-gray-900">

            <main className="max-w-6xl mx-auto px-4 md:px-8 py-8 md:py-16">
                
                {/* Breadcrumb / Back */}
                <div className="mb-8">
                    <Link href="/pelanggan/produk" className="inline-flex items-center text-gray-500 hover:text-[#D93F21] transition-colors font-medium">
                        <ArrowLeft className="w-4 h-4 mr-2" /> Kembali ke Menu
                    </Link>
                </div>

                {/* --- SECTION DETAIL PRODUK --- */}
                <div className="flex flex-col lg:flex-row gap-12 lg:gap-16 mb-24">
                    
                    {/* KIRI: GAMBAR */}
                    <div className="w-full lg:w-1/2">
                        <div className="relative aspect-[4/3] rounded-[2rem] overflow-hidden shadow-xl bg-gray-100 group">
                            <Image 
                                src={product.foto_url || '/images/placeholder-food.jpg'} 
                                alt={product.nama_produk}
                                fill
                                className="object-cover transition-transform duration-700 group-hover:scale-105"
                                unoptimized
                            />
                        </div>
                    </div>

                    {/* KANAN: INFO */}
                    <div className="w-full lg:w-1/2 flex flex-col justify-center">
                        {/* Kategori Badge */}
                        <div className="flex gap-2 mb-4">
                            <Badge className="bg-[#D93F21] hover:bg-[#b9351b] text-white px-4 py-1 rounded-full text-xs uppercase tracking-wider">
                                {product.kategori?.nama_kategori}
                            </Badge>
                            {product.is_tersedia ? (
                                <Badge className="bg-green-100 text-green-800 hover:bg-green-200 border-green-200 px-4 py-1 rounded-full text-xs uppercase tracking-wider">
                                    Available
                                </Badge>
                            ) : (
                                <Badge variant="destructive" className="px-4 py-1 rounded-full text-xs uppercase tracking-wider">
                                    Sold Out
                                </Badge>
                            )}
                        </div>

                        <h1 className="text-4xl md:text-5xl font-extrabold text-gray-900 mb-4 leading-tight">
                            {product.nama_produk}
                        </h1>

                        <p className="text-gray-500 text-lg leading-relaxed mb-6">
                            {product.deskripsi || "Menu spesial dengan cita rasa otentik khas MainDiHatta. Dibuat dari bahan-bahan segar pilihan."}
                        </p>

                        {/* Rating (Statis Visual Saja) */}
                        <div className="flex items-center gap-1 mb-8">
                            {[1, 2, 3, 4, 5].map((star) => (
                                <Star key={star} className="w-5 h-5 text-yellow-400 fill-yellow-400" />
                            ))}
                            <span className="text-gray-400 text-sm ml-2">(4.8 Reviews)</span>
                        </div>

                        <div className="h-px bg-gray-100 w-full mb-8"></div>

                        <div className="flex items-end justify-between">
                            
                            {/* Quantity */}
                            <div>
                                <p className="text-sm font-bold text-gray-900 mb-3">Quantity</p>
                                <div className="flex items-center gap-4">
                                    <button 
                                        onClick={handleDecrement}
                                        className="w-10 h-10 rounded-full border border-gray-200 flex items-center justify-center hover:bg-[#D93F21] hover:text-white hover:border-[#D93F21] transition-all"
                                    >
                                        <Minus className="w-4 h-4" />
                                    </button>
                                    <span className="text-xl font-bold text-gray-900 w-8 text-center">{quantity}</span>
                                    <button 
                                        onClick={handleIncrement}
                                        className="w-10 h-10 rounded-full border border-gray-200 flex items-center justify-center hover:bg-[#D93F21] hover:text-white hover:border-[#D93F21] transition-all"
                                    >
                                        <Plus className="w-4 h-4" />
                                    </button>
                                </div>
                            </div>

                            {/* Total Price & Order */}
                            <div className="text-right">
                                <p className="text-3xl font-extrabold text-[#D93F21] mb-4">
                                    {formatRupiah(Number(product.harga_jual) * quantity)}
                                </p>
                                <Button 
                                    onClick={handleOrder}
                                    disabled={!product.is_tersedia}
                                    className="bg-[#D93F21] hover:bg-[#b9351b] text-white px-8 py-6 rounded-full font-bold text-lg shadow-lg shadow-orange-200 transition-transform hover:scale-105 active:scale-95"
                                >
                                    Order Now <ShoppingCart className="ml-2 w-5 h-5" />
                                </Button>
                            </div>
                        </div>
                    </div>
                </div>

                {/* --- SECTION PRODUK LAINNYA --- */}
                <div className="mt-20">
                    <h3 className="text-2xl font-bold text-[#D93F21] mb-8 border-l-4 border-[#D93F21] pl-4">
                        Produk Lainnya
                    </h3>
                    
                    <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6">
                        {relatedProducts.map((item) => (
                            <Link key={item.id} href={`/pelanggan/produk/${item.id}`}>
                                <div className="group bg-white rounded-2xl p-3 border border-gray-100 hover:border-[#D93F21]/30 hover:shadow-xl transition-all duration-300 cursor-pointer">
                                    <div className="relative h-40 w-full rounded-xl overflow-hidden mb-3 bg-gray-100">
                                        <Image 
                                            src={item.foto_url || '/images/placeholder-food.jpg'} 
                                            alt={item.nama_produk}
                                            fill
                                            className="object-cover group-hover:scale-110 transition-transform duration-500"
                                            unoptimized
                                        />
                                        <div className="absolute bottom-2 left-2 bg-white/90 px-2 py-0.5 rounded-md text-[10px] font-bold text-gray-800">
                                            {item.kategori?.nama_kategori}
                                        </div>
                                    </div>
                                    <div>
                                        <h4 className="font-bold text-gray-900 line-clamp-1 group-hover:text-[#D93F21] transition-colors">
                                            {item.nama_produk}
                                        </h4>
                                        <div className="flex justify-between items-center mt-2">
                                            <p className="text-sm text-gray-500">Rp {Number(item.harga_jual).toLocaleString('id-ID')}</p>
                                            <div className="w-7 h-7 rounded-full bg-[#D93F21] flex items-center justify-center text-white shadow-md scale-0 group-hover:scale-100 transition-transform">
                                                <ArrowRight className="w-3 h-3" />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </Link>
                        ))}
                    </div>
                </div>

            </main>
        </div>
    );
}